.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.emoji-mart {
  position: absolute;
  bottom: 90px;
  left: 0px;
}
.toggle-emoji svg {
  width: 28px;
  height: 28px;
}

.inputMagic{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.inputMagic input{
  flex-basis : 80%;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}